<!-- 项目管理页面 -->
<template>
	<view>
		<LoadMoreList searchField="projectName" apiUrl="/project/getList" height="calc(100vh - 126rpx)" placeholder="输入项目名称搜索">
			<template #default="{ item }">
				<u-cell center @click="click(item)">
					<view slot="title" class="u-slot-title">
						<text class="name" style="font-size: 35rpx;">{{ item.projectName }}</text>
						<view style="margin-top: 12rpx;">
							<u--text type="info" :text="item.projectState == 0 ? '存续': '已完结'"></u--text>
						</view>
					</view>
					<view slot="value" class="u-slot-value">
						<u--text type="info" :text="item.projectLevel == 0 ? '省级': '市级'"></u--text>
						<view style="margin-top: 12rpx;"><u--text :text="item.projectAttribution"></u--text></view>
					</view>
				</u-cell>
			</template>
		</LoadMoreList>
	</view>
</template>

<script>
	import LoadMoreList from '@/pages_sub/components/LoadMoreList.vue';
	
	export default {
		components: {
			LoadMoreList
		},
		data() {
			return {
				
			};
		},
		methods: {
			click(item) {
				uni.navigateTo({
					url: `/pages_sub/projects/details/details?params=${JSON.stringify(item)}`
				});
			}
		}
	}
</script>

